<template>
  <div>
    <div v-for="img in images" :key="img.id" class="pdfcon">
      <img :src="img.imageUrl" class="imageforpdf" />
    </div>
    <v-card-actions>
      <v-btn class="mx-auto success" large tile @click="downloadPdf"
        >Download PDF</v-btn
      >
    </v-card-actions>
  </div>
</template>

<script>
export default {
  methods: {
    downloadPdf() {
      window.print();
    },
  },
  computed: {
    images: {
      get() {
        return this.$store.state.images;
      },
      set(value) {
        this.$store.commit("updateList", value);
      },
    },
  },
};
</script>

<style lang="scss" scoped>
.pdfcon {
  width: 100vw;
}
.imageforpdf {
  height: 100%;
  width: 100%;
}
</style>